import { motion } from "framer-motion";
import useOrder from "../../hooks/useOrder";
import { useMemo } from "react";
import OrderHistoryCard from "./OrderHistoryCard";
import { useSearchParamsState } from "../../hooks/useSearchParamsState";
import NoOrder from "./NoOrder";
import Pagination from "../../components/Pagination";

const Orders = () => {
  const { orders, loading, error } = useOrder();
  const [page, setPage] = useSearchParamsState("page", 1);
  const limit = 5;

  const currentTableData = useMemo(() => {
    const firstPageIndex = (page - 1) * limit;
    const lastPageIndex = firstPageIndex + limit;
    return orders.slice(firstPageIndex, lastPageIndex);
  }, [page, orders]);

  if (loading) {
    return (
      <div className="w-full">
        <h2 className="text-center font-secondary font-medium text-xl mt-8">
          Loading...
        </h2>
      </div>
    );
  }

  if (error) {
    return (
      <div className="w-full">
        <h2 className="text-center font-secondary font-medium text-xl mt-8">
          {error.message}
        </h2>
      </div>
    );
  }

  return (
    <motion.main
      className="flex-1"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
    >
      <div className="bg-white p-6 rounded-md lg:shadow-md">
        <h2 className="text-2xl font-semibold mb-4">My Orders</h2>

        {orders.length === 0 ? (
          <NoOrder />
        ) : (
          <div className="space-y-4">
            {currentTableData.map((order) => (
              <OrderHistoryCard key={order._id} order={order} />
            ))}

            <Pagination
              currentPage={parseInt(page, 10)}
              totalCount={orders.length}
              pageSize={limit}
              onPageChange={(page) => setPage(page.toString())}
            />
          </div>
        )}
      </div>
    </motion.main>
  );
};

export default Orders;
