import React from "react";
import { getColorForStatus } from "../../utils/Constants";
import Button from "../../components/Button";
import { Link, useNavigate } from "react-router-dom";
import { formatDate } from "../../utils/dateUtils";

const OrderHistoryCard = ({ order }) => {
  const navigate = useNavigate();

  return (
    <div className="bg-white rounded-lg p-4 md:p-6 w-full border">
      <div className="flex flex-col gap-y-4">
        <div className="CARD-HEAD w-max flex items-center justify-between gap-2">
          <span
            className={`text-xxs text-white px-2 py-1 leading-none rounded-full ${getColorForStatus(
              order.status
            )}`}
          >
            {order.status}
          </span>
          <span className="text-gray-600 text-sm">
            {formatDate(new Date(order.updatedAt))}
          </span>
        </div>

        <div className="CARD-BODY flex max-lg:flex-col justify-between gap-4">
          <div className="ORDER-ITEMS w-full flex max-lg:flex-col gap-4">
            {order?.orderItem.slice(0, 1).map((item, index) => (
              <div key={index} className="PRODUCT-MAIN flex gap-2 flex-grow">
                <Link to={`/order/${order._id}`} className="flex-shrink-0">
                  {item.productId ? (
                    <img
                      src={item.productId?.images[0]}
                      alt={item.productId?.productGroup?.name}
                      loading="lazy"
                      className="size-16 rounded"
                    />
                  ) : (
                    <div className="size-16 bg-lightGray flex items-center justify-center text-primary rounded text-xs text-center">
                      Invalid Product
                    </div>
                  )}
                  {order?.orderItem.length > 1 && (
                    <p className="text-xs text-center font-medium">
                      +{order.orderItem.length - 1} more
                    </p>
                  )}
                </Link>
                <div className="text-sm">
                  <h3 className="font-medium">{item.productId?.name}</h3>
                  {item?.productId?.attributes &&
                    Object.keys(item?.productId?.attributes)
                      .slice(0, 3)
                      .map((key) => (
                        <p index={key} className="capitalize">
                          {key}: {item?.productId?.attributes[key]}
                        </p>
                      ))}
                </div>
              </div>
            ))}
            <div className="text-sm font-medium 2xl:w-80 xl:w-60 lg:w-40 flex-shrink-0">
              <h2>Expected delivery on Monday, 12th March</h2>
              <div className="flex gap-2">
                <p>Order date :</p>
                <p>{formatDate(new Date(order.createdAt))}</p>
              </div>
              <div className="flex gap-2">
                <p>Total :</p>
                <p>â‚¹ {order.totalPrice}</p>
              </div>
            </div>
          </div>

          <div className="BUTTONS-SECTION flex flex-col gap-y-2 flex-shrink-0">
            <Button>Track order</Button>
            <Button reverse onClick={() => navigate(`/order/${order._id}`)}>
              View order details
            </Button>
            <div className="flex lg:flex-col justify-around items-center">
              <button className="text-blue-500 text-sm px-4 py-2">
                Get invoice
              </button>
              <button className="text-blue-500 text-sm px-4 py-2">
                Edit address
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default OrderHistoryCard;
