import { useNavigate } from "react-router-dom";
import Button from "../../components/Button";

const NoOrder = () => {
  const navigate = useNavigate();

  return (
    <div className="space-y-6">
      <h4 className="text-lg font-secondary font-medium text-center">
        No order placed
      </h4>
      <ul className="flex justify-center gap-4">
        {Array.from({ length: 3 }).map((_, index) => (
          <li
            key={index}
            className="size-40 max-lg:size-30 max-sm:size-20 bg-mainGray"
          ></li>
        ))}
      </ul>
      <Button
        onClick={() => navigate("/wishlist")}
        className="px-6 py-3 rounded-full text-lg mx-auto"
      >
        GO TO WISHLIST
      </Button>
    </div>
  );
};

export default NoOrder;
