import { FiChevronLeft, FiChevronRight } from "react-icons/fi";
import { DOTS, usePagination } from "../hooks/usePagination";
import { Fragment } from "react";

const Pagination = ({
  onPageChange,
  totalCount,
  siblingCount = 1,
  currentPage,
  pageSize,
}) => {
  const paginationRange = usePagination({
    currentPage,
    totalCount,
    siblingCount,
    limit: pageSize,
  });

  const lastPage = paginationRange[paginationRange.length - 1];

  if (currentPage === 0 || paginationRange.length < 2) {
    return null;
  }

  const onNext = () => {
    if (currentPage >= lastPage) return;
    onPageChange(currentPage + 1);
  };

  const onPrevious = () => {
    if (currentPage <= 1) return;
    onPageChange(currentPage - 1);
  };

  return (
    <div className="my-8">
      <ul className="relative flex gap-4 max-md:gap-3 w-max mx-auto">
        {paginationRange.map((pageNumber, index) => (
          <Fragment key={index}>
            {pageNumber === DOTS && (
              <li className="relative h-max top-1">{DOTS}</li>
            )}

            {pageNumber !== DOTS && (
              <li
                className={`size-10 max-lg:size-8 max-md:size-6 border border-black max-lg:text-sm max-md:text-xs flex justify-center items-center cursor-pointer transition-colors duration-200 ease-in-out ${
                  pageNumber === currentPage
                    ? "bg-primeRed text-white"
                    : "bg-white"
                } ${pageNumber !== currentPage && "hover:bg-mainGray"}`}
                onClick={() => onPageChange(pageNumber)}
              >
                {pageNumber}
              </li>
            )}
          </Fragment>
        ))}

        <button
          className={`absolute -left-10 top-1/2 -translate-y-1/2 ${
            currentPage <= 1 && "text-primary"
          }`}
          onClick={onPrevious}
        >
          <FiChevronLeft size={28} />
        </button>

        <button
          className={`absolute -right-10 top-1/2 -translate-y-1/2 ${
            currentPage >= lastPage && "text-primary"
          }`}
          onClick={onNext}
        >
          <FiChevronRight size={28} />
        </button>
      </ul>
    </div>
  );
};

export default Pagination;
